
































import Vue from "vue"

export default Vue.extend({
  name: "NavBar",
  data() {
      return {
          src: ''
      }
  },
  props: {
      url: {
          type: String,
          default: ''
      }
  },
  watch: {
    url: {
      handler(newValue) {
        this.src = newValue
      }
    }
  }
})
